

.tool-block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0.8vw;
}


.tool-block > img {
    max-width: 64px;
    max-height: 64px;
    min-width: 25px;
    min-height: 25px;
    width: 3vw;
    padding: 1vw;
    object-fit: cover;
}

.tool-container {
    display: flex;
    width:100%;
    flex-direction: column;
    align-items: center;
    padding: 0 0 5vh 0;
}

.tool-title {
    font-family: "Coolvetica";
}

.tool-logoname {
    display: flex;
    width: 100%;
    max-width: 700px;
    justify-content: space-between;
}