.contentAbout {
    display: flex;
    flex-direction: column;
    padding: 5vh 15vw 0 15vw;
}

.contentAbout .title {
    font-family: "Kaoly";
    color: white;
    font-size: 10vw;
    padding-top: 10rem;
}

.contentAbout .first-block {
    text-align: justify;
    text-justify: inter-word;
}

.contentAbout .portrait-container {
    float: left;
    padding: 0 5vw 5vh 0;
}

.contentAbout .portrait {
    height: 40vh;
    border-radius: 15%;
}

LogoNameBlock {
    display: flex;
    flex-direction: column;
}

.contentAbout .second-block {
    display: flex;
    flex-direction: column;
    align-items:center;
}

